review-container {
	padding: 20px;
}

.review-container h2 {
	text-align: center;
}

.review-grid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
	gap: 30px;
	margin-bottom: 50px;
	/* box-shadow: 1px 1px 2px var(--primary-color-One); */

}

.review-item {
	background-color: var(--secondary-color-Two);
	padding: 20px;
	border-radius: 8px;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
	transition: transform 0.3s ease, box-shadow 0.3s ease;
	margin: 30px;
}

.review-item:hover {
	box-shadow:var(--secondary-color-One);
	transform: translateY(-10px);
	box-shadow: 0 15px 30px rgba(0,0,0,0.12);
}

.review-item h3 {
	font-size: 1.5em;
	color: var(--secondary-color-One);
	text-shadow: 1px 1px 2px var(--secondary-color-Two);
	margin-bottom: 10px;
}

.review-item .description {
	color: var(--accent-color-One);
	text-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
	margin-bottom: 15px;
	font-style: italic;
}

.review-item .rating {
	font-size: 1.2em;
	font-weight: bold;
	color: var(--accent-color-Three);
} 

.fa {
	padding: 20px;
	font-size: 30px;
	width: 30px;
	text-align: center;
	text-decoration: none;
	border-radius: 50%;
}

.social-media-container {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 40px 0;
	margin-top:10px;
}

.social-item {
	background-color: var(--primary-color);
	padding: 50px;
	border-radius: 8px;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
	transition: transform 0.3s ease, box-shadow 0.3s ease;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50%;
	gap: 20px;
}

.social-items i {
	color: var(--text-color);
	transform: scale(1.25);
	transition: color 0.3s;
}

.social-items i:hover {
	transform: translateY(-5px);
	box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.social-item a:link,
.social-item a:visited,
.social-item a:hover,
.social-item a:active {
	color: var(--secondary-color);
	text-decoration: none;
}

@media (max-width: 768px){
	.social-item{
		width:100%;
	}
}

button4 {
  /* Variables */
  --button_radius: 0.75em;
  --button_color: #e8e8e8;
  --button_outline_color: #000000;
  font-size: 17px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  border-radius: var(--button_radius);
  background: var(--button_outline_color);
}

.button_top {
  display: block;
  box-sizing: border-box;
  border: 2px solid var(--button_outline_color);
  border-radius: var(--button_radius);
  padding: 0.75em 1.5em;
  background: var(--button_color);
  color: var(--button_outline_color);
  transform: translateY(-0.2em);
  transition: transform 0.1s ease;
}

button4:hover .button_top {
  /* Pull the button upwards when hovered */
  transform: translateY(-0.33em);
}

button4:active .button_top {
  /* Push the button downwards when pressed */
  transform: translateY(0);
}

.ad-container {
	background-color: var(--primary-color-One);
	color: var(--secondary-color-Two);
	padding: 15px 0;
	overflow: hidden;
	white-space: nowrap;
}

.scrolling-ad{
	display: inline-block;
	animation: scroll-left 20s linear infinite;
	font-size: 1rem;
	font-weight:600;

}

.scrolling-ad span{
	margin-right:50px;
}

@keyframes scroll-left{
	0%{
		transform: translateX(0);
	}
	100%{
		transform:translateX(-50%);
	}
}

.footer {
	background-color: var(--secondary-color-One);
	color: var(--secondary-color-Two);
	padding: 40px 0 20px;
}

.footer-content {
	display: grid;
	grid-template-columns: 1fr;
	gap: 30px;
	margin-bottom: 30px;
	text-align: center;
}

.footer-section h7 {
	/* color: var(--secondary-color-Three); */
	margin-bottom: 15px;
	font-size: 1.2rem;
}

.footer-section p, .footer-section ul, .footer-section a, .footer-section address {
	color: var(--secondary-color-Two);
	text-decoration: none;
	font-style: normal;
}

.buzzword-wrapper {
	display: inline-block;
	height: 1.2em; /* Adjust based on line-height */
	line-height: 1.2em;
	overflow: hidden;
	vertical-align: bottom; /* Aligns the container with the text */
}

.buzzword {
	display: inline-block;
	color: #ebc250;
	font-weight: 600;
	transform: translateY(0);
}

@keyframes roll-out {
	from {
		transform: translateY(0);
		opacity: 1;
	}
	to {
		transform: translateY(-100%);
		opacity: 0;
	}
}

@keyframes roll-in {
	from {
		transform: translateY(100%);
		opacity: 0;
	}
	to {
		transform: translateY(0);
		opacity: 1;
	}
}

.footer-section ul {
	list-style: none;
	align-items:center;
}

.footer-section ul li {
	margin-bottom: 10px;
}

.footer-section span{
	font-size:2rem;
}

.map-container {
	margin-top: 15px;
	border-radius: 8px;
	overflow: hidden;
	filter: grayscale(80%) contrast(1.1);
	transition: filter 0.3s ease;
}

.map-container:hover {
	filter: grayscale(0%);
}

.map-container iframe {
	width: 100%;
	height: 150px;
	border: 0;
	display: block;
}

.social-links {
	display: flex;
	justify-content: center;
	gap: 15px;
}

.social-links a {
	margin-right: 0;
	font-size: 1.2rem;
}

.footer-bottom {
	text-align: center;
	padding-top: 20px;
	border-top: 0px solid var(--secondary-color-Two);
	font-size: 0.9rem;
}


/* CSS button by Michael McMillan */
.button-54 {
  /* font-family: "Open Sans", sans-serif; */
  font-size: 16px;
  letter-spacing: 2px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
  cursor: pointer;
  border: 3px solid;
  padding: 0.25em 0.5em;
  box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
  position: relative;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin-bottom: 20px;
}
.button-54 a{
	color: var(--secondary-color-One);
}

.button-54:active {
  box-shadow: 0px 0px 0px 0px;
  top: 5px;
  left: 5px;
}

@media (min-width: 768px) {
  .button-54 {
	padding: 0.25em 0.75em;
  }
}
